function initReviewsSwiper() {
  const swiper = new Swiper('.reviews-swiper', {
    modules: [Navigation, Pagination, Autoplay],
    loop: true,
    slidesPerView: 'auto',
    centeredSlides: true,
    spaceBetween: 40,
    speed: 700,
    autoHeight: true,
    observer: true,
    observeParents: true,
    observeSlideChildren: true,
    autoplay: {
      delay: 8000,
      disableOnInteraction: false,
    },
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },
    on: {
      init() {
        this.params.autoHeight = false;
        setTimeout(() => {
          this.params.autoHeight = true;
          this.updateAutoHeight(400);
        }, 600);
      },
      slideChangeTransitionEnd() {
        this.updateAutoHeight(300);
      },
    },
  });

  const container = document.querySelector('.reviews-swiper');
  container.addEventListener('click', e => {
    const btn = e.target.closest('.read-more-btn');
    if (!btn) return;

    const content = btn.previousElementSibling;
    if (!content) return;

    content.classList.toggle('expanded');
    btn.textContent = content.classList.contains('expanded')
      ? 'Hide review'
      : 'Read full review';

    swiper.updateAutoHeight(400);
  });

  return swiper;
}

document.addEventListener('DOMContentLoaded', () => {
  renderReviews();
  setTimeout(() => initReviewsSwiper(), 200);
});
